import Vue from 'vue'
const vue = new Vue()
const rules = {
  // 空字符的校验
  especilTest: (rule, value, callback) => {
    if (!value.trim()) {
      callback(new Error('请输入媒资名称'))
    } else {
      callback()
    }
  },
  spaceTest: (rule, value, callback) => {
    if (value.length !== value.trim().length) {
      callback(new Error('输入字符两侧不允许出现空格'))
    } else {
      callback()
    }
  }
}
export default rules
