<script>
import api from '@/api/api';
import rule from '@/libs/rule';
import shuttleBox from './components/shuttle-box.vue';

export default {
  name: 'AdminTable',
  components: {
    shuttleBox,
  },
  data() {
    const validator = rule.spaceTest
    const validatePassword = (rule, value, callback) => {
      if (
        this.formCreated.newPassword1 !== this.formCreated.newPasswordAg1
      ) {
        return callback(new Error("输入的两次密码不一致，请重新输入"))
      } else {
        callback()
      }
    };
    const validatePassword1 = (rule, value, callback) => {
     if (
        this.formModification2.newPassword2 !==
        this.formModification2.newPasswordAg2
      ) {
        return callback(new Error("输入的两次密码不一致，请重新输入"))
      } else {
        callback()
      }
    };
    
    /** 密码重置框 密码长度校验 */
    const validateNewPassword2Length = (rule, value, callback) => {
      if (value === '') {
        this.newPassword2Length = 0;
        callback(new Error("密码不能为空!"));
      } else if (value.length > 20) {
        this.newPassword2Length = value.length;
        callback(new Error("密码过长,请重新输入!"));
      } else {
        this.newPassword2Length = value.length;
        callback();
      }
    };

    /** 密码重置框 确认密码长度校验 */
    const validateNewPasswordAg2Length = (rule, value, callback) => {
      if (value === '') {
        this.newPasswordAg2Length = 0;
        callback(new Error("确认密码不能为空!"));
      } else if (value.length > 20) {
        this.newPasswordAg2Length = value.length;
        callback(new Error("确认密码过长,请重新输入!"));
      } else {
        this.newPasswordAg2Length = value.length;
        callback();
      }
    };

    return {
      currentTabPane: '1', // 授权观看：直播or视频切换，默认为直播
     
      formCreated: {
        newName1: '',
        newPassword1: '',
        newPasswordAg1: '',
        newPhone1: '',
        newEmail1: ''
      },

      formModification: {
        newPhone: '',
        newEmail: ''
      },
      formModification2: {
        newPassword2: '',
        newPasswordAg2: ''
      },
      ruleValidate: {
        //新建
        newName1: [
          {
            required: true,
            message: "请输入操作员名称",
            trigger: 'blur'
          },
          {validator:validator, trigger: 'blur'},
          { max: 20, message: "输入字符长度不允许超出20", trigger: 'blur' }
        ],
        newPassword1: [
          {
            required: true,
            message: "请输入密码",
            trigger: 'blur'
          },
          {
            pattern: /^\S+$/,
            message: "请输入长度4 - 20 的英文、 数字、 特殊字符",
            trigger: 'blur'
          },
          {
            min: 4,
            message: "输入字符长度不允许小于4",
            trigger: 'blur'
          },
          { max: 20, message: "输入字符长度不允许超出20", trigger: 'blur' }
        ],
        newPasswordAg1: [
          { required: true, trigger: 'blur',message:"请输入重复密码" },
          { max: 20, message: "输入字符长度不允许超出20", trigger: 'blur' },
          { validator: validatePassword}
        ],
        newPhone1: [
          { required: false, trigger: 'blur' },
          {validator:validator,trigger:'blur'},
          { max: 20, message: "输入字符长度不允许超出20", trigger: 'blur' }
        ],
        newEmail1: [
          { required: false, trigger: 'blur' },
          {
            pattern: /^([0-9A-Za-z\-_\.]+)@([0-9a-z]+\.[a-z]{2,3}(\.[a-z]{2})?)$/g,
            message: "请输入有效的邮箱",
            trigger: 'blur'
          },
          {
            max: 200,
            message: "输入字符长度不允许超出200",
            trigger: 'blur'
          }
        ],
        //修改操作员
        newPhone: [
          { required: false, trigger: 'blur' },
          {validator:validator,trigger:'blur'},
          { max: 20, message: "输入字符长度不允许超出20", trigger: 'blur' }
        ],
        newEmail: [
          { required: false, trigger: 'blur' },
          {
            pattern: /^([0-9A-Za-z\-_\.]+)@([0-9a-z]+\.[a-z]{2,3}(\.[a-z]{2})?)$/g,
            message: "请输入有效的邮箱",
            trigger: 'blur'
          },
          {
            max: 200,
            message: "输入字符长度不允许超出200",
            trigger: 'blur'
          }
        ],
        //重置密码
         newPassword2: [
          { required: true, validator: validateNewPassword2Length, trigger: 'change'},
          {
            required: true,
            message: "请输入密码",
            trigger: 'blur'
          },
          // {
          //   pattern: /^\S+$/,
          //   message: "请输入长度4 - 20 的英文、 数字、 特殊字符",
          //   trigger: 'blur'
          // },
          {
            pattern: /^(?![a-zA-z]+$)(?!\d+$)(?![!@#$&*]+$)[a-zA-Z\d!@#$&*]+$/,
            message: '请输入长度4-20位英文、 数字、特殊字符（!，@，#，$，&，*）任意两种组合',
            trigger: 'blur'
          },
          {
            min: 4,
            message: "输入字符长度不允许小于4",
            trigger: 'blur'
          },
          { max: 20, message: "输入字符长度不允许超出20", trigger: 'blur' }
        ],
        newPasswordAg2: [
          { required: true, validator: validateNewPasswordAg2Length, trigger: 'change'},
          { required: true, trigger: 'blur', message:"请输入重复密码" },
          { max: 20, message: "输入字符长度不允许超出20", trigger: 'blur' },
          {validator: validatePassword1, trigger: 'blur'}
        ],
      },
      stateList: [
        {
          value: 1,
          label: "已启用"
        },
        {
          value: 0,
          label: "未启用"
        },
        {
          value: 'all',
          label: "全部"
        }
      ],

      channelIds:[], // 已勾选的直播通道唯一id 数组
      channelName:'', // 视频授权观看----通道名称
      choosedProgramData:[], // 勾选的节目数组
      livePermissionArray:[ // 授权观看---直播权限树
        {
          title:'',
          expand:'',
          checked:'',
          disabled:'',
          category_id:'',
          children:[
            {
              title:'',
              expand:'',
              checked:'',
              channel_id:'',
              is_auth:'',
            }
          ]
        }
      ],

      show: true,
      res1: [],
      searchConName3: '', // 首页搜索
      searchConName2: '', // 编辑角色搜索
      searchConName1: '', // 新建角色搜索
      searchDescription1: '', // 描述搜索
      searchDescription2: '', // 描述搜索

      columns1: [
        {
          type: 'selection',
          width: 34,
          // align: 'center'
        },
        {
          key: 'username',
          title: "操作员名称",
          // align: 'center'
          minWidth: 110
        },
        {
          key: 'roleName',
          title: "角色",
          // align: 'center'
          minWidth: 108
        },
        {
          key: 'loginIp',
          title: "最后登录IP",
          // align: 'center'
          minWidth: 140
        },
        {
          key: 'loginDate',
          title: "最后登录时间",
          // align: 'center'
          minWidth: 166
        },
         {
          key: 'login_status',
          title: "登录状态",
          minWidth: 100
        },
        {
          key: 'isEnabled',
          title: "启用状态",
          // align: 'center'
          minWidth: 100
        },
