    /** 修改 操作员信息 */
    updateAdminInfo(operatorParam){
      if(operatorParam) {
        sessionStorage.setItem('operatorParam', JSON.stringify(operatorParam))
      }
      this.$router.push({
        name:'admin-update'
      })
    },

    init() {
      /* 渲染自定义组件 */
      let vm = this
      this.columns1.forEach(item => {
        if (item.key === 'handle') {
          item.render = (h, param) => {
            return h('p', [
              h(
                'span',
                {
                  props: {
                    type: 'text',
                  },
                  style: {
                    marginRight: '4px',
                    color:this.isGray?'gray':'#02E8FF',
                    cursor:this.isGray?'not-allowed':'pointer',
                  },
                  on: {
                    click: function() {
                      if(vm.isGray){
                        return false;
                      }
                      vm.updateAdminInfo(param.row);
                      
                    }
                  }
                },
                "修改"
              ),
              h(
                'span',
                {
                  props: {
                    type: 'text',
                  },
                  style: {
                    marginRight: '4px',
                    color:this.isGray?'gray':'#02E8FF',
                    cursor:this.isGray?'not-allowed':'pointer',
                  },
                  on: {
                    click: function() {
                      if(vm.isGray){
                        return false;
                      }
                      vm.handleReset('formModification2')
                      vm.newPassword2Length = 0;
                      vm.newPasswordAg2Length = 0;
                      vm.modal6 = true
                      vm.userId = param.row.userId
                    }
                  }
                },
                "密码重置"
              ),
              h(
                'span',
                {
                  props: {
                    type: 'text',
                  },
                  style: {
                    color:this.isGray?'gray':'#02E8FF',
                    cursor:this.isGray?'not-allowed':'pointer',
                  },
                  on: {
                    click: function() {
                      if(vm.isGray){
                        return false;
                      }
                      vm.modal7 = true
                      vm.currentRow = param.row;
                      vm.currentTabPane = '1';
                      vm.queryLivePermissionList(param.row.userId);
                      vm.cancelUserProgram(param.row.userId);
                    }
                  }
                },
                "授权观看"
              )
            ])
          }
        }
      })
    },

    /** 授权观看----查询直播授权列表 */
    queryLivePermissionList(userId){
      if(userId) {
        let param = {
          user_id:userId,
        }
        
        this.livePermissionArray = [];
        api.queryLivePermissionJson(param).then(
          res => {
            if(res.result){
              for(let item of res.result){
                let obj = {};
                obj.title = item.category_name;
                obj.expand = true;
                obj.category_id = item.category_id;
                obj.children = [];
                for(let item1 of item.channels){
                  let childObj = {};
                  childObj.title = item1.channel_name;
                  childObj.expand = true;
                  childObj.channel_id = item1.channel_id;
                  childObj.is_auth = item1.is_auth;
                  if(item1.is_auth == 1){
                    childObj.checked = true;
                  }
                  obj.children.push(childObj);
                }
                this.livePermissionArray.push(obj);
              }
            } 
          }
        )
      }
    },

    /** 进入授权观看Modal框，需先调 “取消用户视频授权接口” */
    cancelUserProgram(userId) {
      if(userId) {
        let param = {
          userId:userId,
        }

        api.cancelUserProgram(param).then(
          res => {
            this.$refs.videoShuttleBox.pageNoLeft = 1;
            this.$refs.videoShuttleBox.pageNoRight = 1;
            this.$refs.videoShuttleBox.videoSearchLeft(this.currentRow.userId);
            this.$refs.videoShuttleBox.videoSearchRight(this.currentRow.userId);
          }
        )
      }
    },

    /** 授权观看-----取消 */ 
    cancelPermission(){
      if (this.currentRow.userId) {
        let param = {
          userId:this.currentRow.userId,
        }

        api.cancelUserProgram(param).then(
          res => {
            
          }
        )
      }

      this.modal7 = false;
      this.handleSearch1();  
    },

    /** 授权观看-----确定 */ 
    confirmPermission(){
      let checkedNodesData = this.$refs.livePermissionTree.getCheckedNodes();
      this.channelIds = [];
      for(let item of checkedNodesData) {
        if(item.channel_id) {
          this.channelIds.push(item.channel_id);
        }
      }
         
      this.updateChannelAndVideoPermission();
      this.handleSearch1(); 
    },

    /** 修改直播授权列表 和 录像授权列表 */
    updateChannelAndVideoPermission(){
      let param = {
        user_id:this.currentRow.userId,
        channels:this.channelIds,
      }

      api.updateUserChannelAuth(param).then(
        res => {
          // this.$Message.success('直播授权列表修改成功!');
          this.channelIds = [];
          this.$refs.videoShuttleBox.saveUserProgram(this.currentRow.userId);
          this.modal7 = false;
        }
      )
    },

    /** 删除气泡确认 */
    ok() {
      if (this.selectarr.length < 1) {
        this.$Message.error("请勾选操作员后，再进行操作")
        return false
      }
      let param = {
        ids: this.idsList,
        userIds: this.userIdsList
      }
    
      api.deleteJson(param).then(
        res => {
          if (res.status && res.status === '1222') {
            if ( this.pagetotal - (this.pageNo - 1) * this.pageSize === this.selectarr.length && this.pageNo > 1)
            {
              this.pageNo--
            }
            this.handleSearch1()
            return false
          }
        
          if (
            this.pagetotal - (this.pageNo - 1) * this.pageSize ===
              this.selectarr.length &&
            this.pageNo > 1
          ) {
            this.pageNo--
          }
          this.handleSearch1()
          this.$Message.success("操作员删除成功")
        },
        err => {
          this.handleSearch1()
        }
      )
    },

